import { Link } from 'gatsby';
import React from 'react'
import './style.css';
import { ButtonTypes } from './constants';

const PrimaryButton = (props: any) => {
  return (
    <div className={`${props.className || ""}`}>
      {props.to!=null ? (
        <Link className={`custom-button button-primary px-3 py-1`} to={props.to}>
            {props.children}
        </Link>
      ) : (
        <button className={`custom-button button-primary px-3 py-1`} onClick={() => {props.onClick()}}>
            {props.children}
        </button>
      )}
    </div>
  )
}

export default PrimaryButton;
